import React, { Component, Fragment } from 'react';
import InputMask from 'react-input-mask';
import style from './contactsForm.module.css';
import submit from './submit';
import textSuccess from '../../../../img/elements/textSuccess.png';
import textSuccessW from '../../../../img/elements/textSuccess.webp';
import textError from '../../../../img/elements/textError.png';
import textErrorW from '../../../../img/elements/textError.webp';

export default class ConatactsForm extends Component {
  state = {
    name: '',
    phoneNumber: '',
    email: '',
    responseSuccess: '',
    responseErr: '',
  };

  closeText = () => this.setState({ responseSuccess: '', responseErr: '' });

  setStatusTextSuccess = () => {
    this.setState({
      responseSuccess:
        'Ваша заявка принята!\nС Вами свяжутся в ближайшее время',
    });
  };

  setStatusTextError = () => {
    this.setState({
      responseErr:
        'При отправке заявки возникла ошибка.\nПожалуйса, попробуйте еще раз',
    });
  };

  onHandleChange = e => {
    const { target } = e;
    this.setState({ [target.name]: target.value });
  };

  fetchData = (name, phoneNumber, email) => {
    submit(name, phoneNumber, email)
      .then(response => {
        if (response.data.code === 200) this.setStatusTextSuccess();
        else {
          this.setStatusTextError();
        }
      })
      .catch(error => {
        this.setStatusTextError();
      });
  };

  onHandleSubmit = e => {
    e.preventDefault();
    const { name, phoneNumber, email } = this.state;
    this.fetchData(name, phoneNumber, email);
    this.setState({ name: '', phoneNumber: '', email: '' });
  };

  render() {
    const {
      name,
      phoneNumber,
      email,
      responseSuccess,
      responseErr,
    } = this.state;
    return (
      <Fragment>
        <h3 className={style.header}>связаться с нами</h3>
        <form
          onClick={this.closeText}
          className={style.form}
          onSubmit={this.onHandleSubmit}
        >
          <input
            className={style.input}
            minLength={3}
            required
            name="name"
            onChange={this.onHandleChange}
            type="text"
            placeholder="Имя"
            value={name}
          />
          <div>
            <InputMask
              className={style.input}
              required
              name="phoneNumber"
              onChange={this.onHandleChange}
              type="tel"
              placeholder="Телефон"
              value={phoneNumber}
              mask="+3 (8099) 999-99-99"
            />
          </div>
          <input
            className={style.input}
            name="email"
            onChange={this.onHandleChange}
            type="email"
            placeholder="Email"
            value={email}
          />
          <button className={style.submit} type="submit">
            Отправить
            {responseErr && (
              <span className={style.statusTextErr}>
                <picture>
                  <source srcSet={textErrorW} type="image/webp" />
                  <source srcSet={textError} type="image/png" />
                  <img className={style.icon} src={textError} alt="icon" />
                </picture>
                {responseErr}
              </span>
            )}
            {responseSuccess && (
              <span className={style.statusTextSuccess}>
                <picture>
                  <source srcSet={textSuccessW} type="image/webp" />
                  <source srcSet={textSuccess} type="image/png" />
                  <img className={style.icon} src={textSuccess} alt="icon" />
                </picture>
                {responseSuccess}
              </span>
            )}
          </button>
        </form>
      </Fragment>
    );
  }
}
